import { HomeCrumb } from "@/components/bread-crumb";
import { Container } from "@/components/container";
import { returnPolicyData } from "@/data/return-data";
import Link from "next/link";

const ReturnProduct = () => {
  return (
    <Container>
      <HomeCrumb paths={[{ name: "Возврат товара" }]} />
      <div className="p-4 max-w-3xl mx-auto text-gray-700">
        {returnPolicyData.map((section, sectionIndex) => (
          <div key={sectionIndex} className="mb-6">
            <h2 className="text-lg font-semibold mb-2">{section.title}</h2>
            {section.subpoints?.map((sub, subIndex) => (
              <div key={subIndex} className="ml-4 mb-4">
                <h3 className="text-md font-medium">{sub.title}</h3>
                {sub.details && (
                  <ul className="list-disc ml-6 mt-2">
                    {sub.details.map((detail, detailIndex) => (
                      <li key={detailIndex}>
                        {detail.text}{" "}
                        <span className="text-blue-500">{detail.law}</span>
                      </li>
                    ))}
                  </ul>
                )}
                {sub.steps && (
                  <ul className="list-none ml-6 mt-2">
                    {sub.steps.map((step, stepIndex) => (
                      <li key={stepIndex} className="mb-1">
                        <Link
                          href={step.link}
                          className="text-blue-500 hover:underline"
                        >
                          {step.text}
                        </Link>
                      </li>
                    ))}
                  </ul>
                )}
              </div>
            ))}
          </div>
        ))}
      </div>
    </Container>
  );
};

export default ReturnProduct;
